/* ckeditor5-block-quote/theme/blockquote.css */
.ck-content[dir="rtl"] blockquote {
  border-left: 0;
  border-right: solid 5px #cccccc; }

/* ckeditor5-table/theme/table.css */
.ck-content .table {
  margin: 1em auto;
  display: table; }

/* ckeditor5-table/theme/table.css */
.ck-content .table table {
  border-collapse: collapse;
