/* ckeditor5-table/theme/table.css */
.ck-content .table table td,
.ck-content .table table th {
  min-width: 2em;
  padding: .4em;
  border: 1px solid #bfbfbf; }

/* ckeditor5-table/theme/table.css */
.ck-content .table table th {
